*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    max-width: 100%;
}
.mn{
    height:12vh;
    width:100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.copy{
    padding:10px;
    font-family: "Poppins";
    font-size: 30px;
    margin-left: 0.4em;
}
@media screen and (min-width:768px) {
.mn{
    height:12vh;
    width:100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.copy{
    padding:10px;
    font-family: "Poppins";
    font-size: 50px;
    margin-left: 0.4em;
}    
}