*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    max-width: 100%;
}
.lowmid{
    height: 210vh;
    width:100vw;
    display: flexbox;
    justify-content: space-evenly;
    overflow: hidden;
}
.textndimages{
    margin-top: 5em;
    display: flexbox;
    justify-content: center;
    align-items: center;
}
.Image1{
    height:80vh;
    width:80vw;
    background: url('Pi3.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 2.7em;
}
.Text{
    margin-top: 3em;
    margin-top: 1.5em;
}
.hd{
    font-family: "Poppins";
    font-size: 30px;
    margin-left: 0.4em;
}
.hlist{
    height:100vh;
    background: rgb(255, 255, 255);
    margin-top: 1.5em;
}
.sublist{
    display: flexbox;
    margin: 1em;
    list-style: none;
    font-family: "Poppins";
    font-size: 20px;
    padding: 50px 0;
}
@media screen and (min-width:768px){
    .lowmid{
        height: 150vh;
        width:100vw;
        display: flexbox;
        justify-content: space-evenly;
        overflow: hidden;
    }
    .textndimages{
        margin-top: 5em;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }
    .Image1{
        height:100vh;
        width:50vw;
        background: url('Pi3.jpg');
        background-size: cover;
        background-position: top top;
        background-repeat: no-repeat;
    }
    .Text{
        margin-top: 3em;
        
        width:50vw;
    }
    .hd{
        font-family: "Poppins";
        font-size: 40px;
        margin-left: 0.4em;
    }
    .hlist{
        height:100vh;
        background: rgb(255, 255, 255);
        margin-top: 1.5em;
    }
    .sublist{
        display: flexbox;
        margin-left: 1.4em;
        list-style: none;
        font-family: "Poppins";
        font-size: 25px;
        
    }
}
@media screen and (max-width:360px) {
    .lowmid{
        height: 250vh;
        
    }
}