*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    max-width: 100%;
}
.middile{
    height:90vh;
    width:100vw;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.text-image{
    display: block;
    align-items: center;
    justify-content: center;
}
.text{
    margin-top: 3em;
}
.heading{
    font-family: "Poppins";
    font-size: 30px;
    margin-left: 0.4em;
}
.mainlist{
    background: rgb(255, 255, 255);
    height:30vh;
    margin-top: 1.5em;
}
.sub{
    display: flexbox;
    height:5vh;
    margin-left: 1.4em;
    justify-content: space-evenly;
    list-style: none;
    font-family: "Poppins";
    font-size: 22px;
}
.image{
    height:40vh;
    width:100vw;
    background: url('Pi12.jpg');
    background-size: cover;
}
@media screen and (min-width:768px){
    .text-image{
        display: flex;
        align-items: center;
        justify-content:space-around;
        margin-top: 10em;
