*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    max-width: 100%;
}
.bod{
    height:100vh;
    width:100vw;
    background: url('boat.png');
    background-size: cover;
    background-repeat: no-repeat;
}
.navbar{
    display:flex;
    width: 100vw;
    height:10vh;
    /* background: rgba(172, 82, 97,0); */
    align-items: center;
    justify-content: center;
    
}
.log{
    color: white;
    font-family: "Poppins";
    
    cursor: pointer;
}
.nnav{
    display:flex;
    width: 80vw;
    justify-content: space-between;
}
.info{
    display: flex;
    width:40vw;
    color: white;
    font-family: "Poppins";
    justify-content: space-evenly;
    
}
.home,.about{
    font-size: 21px;
    margin-top: 5px;
}
.home:hover{
    transition: 0.5s ease-in-out;
    color:rgb(155, 223, 255);
    text-decoration: underline;
    cursor: pointer;
}
.about:hover{
    transition: 0.5s ease-in-out;
    color:rgb(155, 223, 255);
    text-decoration: underline;
    cursor: pointer;
}
.main-body{
    height:80vh;
    width:100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
}
.center{
   
    width:60vw;
    display: flex;
    align-items: center;
    justify-content: center;
   
}
.click{
    color: white;
    font-family: "Poppins";
    font-size: 35px;
    cursor: pointer;
}

@media screen and (max-width:360px) {
    .info{
        font-size: 15px;
       
        color: rgb(255, 255, 255);
        width:12em;
        display: flex;
        justify-content: space-around;
        
    }
    .click{
        color: white;
        font-family: "Poppins";
        font-size: 25px;
        cursor: pointer;
    }
    
}